.Hire__Form input, .Hire__Form label, .Hire__Form select, .Hire__Form textarea{
    width: 65%;
    text-align: left;
    border-radius: 5px;
}

.Hire__Form input, .Hire__Form select, .Hire__Form textarea{
    outline: none;
    border: 1px solid #04dcca;
    margin-bottom: 2%;
}

.Hire__Form textarea{
    height: 250px;
}

.Hire__Form label{
    margin-bottom: -2%;
}

.Hire{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
}

.Hire__Form h1{
    color: #6e6262;
    padding-top: 4%;
    text-transform: uppercase;
    padding-bottom: 2%;
}

.Hire__Side{
    background: linear-gradient(to right, #04dccae8, #07d97ae7);    
    background-attachment: fixed;
    height: 850px;
    width: 100%;
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: contain;
}

.Hire__Side img{
    width: 70%;
    padding-top: 25%;
}

.Hire__Form p{
    font-style: italic;
    font-size: 14px;
    width: 70%;
    margin: auto;
    margin-top: -2%;
    margin-bottom: 4%;
}

.Hire__btn{
    background: #04dcca;
    color: #fff;
    padding: 4px 180px;
    border: none;
    border-radius: 5px;
    outline: none;
    font-weight: bold;
    margin-top: -10%;
}

.Hire__Side p{
    color: #fff;
    width: 85%;
    margin: auto;
    font-style: italic;
}

.months{
    font-style: italic;
    color: #6e6262;
    font-size: 12px;
}

/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    
}

/* Devices under 768px (md) */

@media(max-width: 767px){
    .Hire{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
    .Hire__btn{
        padding: 4px 80px;
        margin-bottom: 5%;
    }
    .Hire__Form p{
        font-size: 12px;
        line-height: 1.2;
        width: 60%;
        margin: auto;
        margin-bottom: 4%;
    }
    .Hire__Form input, .Hire__Form label, .Hire__Form select, .Hire__Form textarea{
        width: 75%;
        font-size: 14px;
    }
    .Hire__Form h1{
        font-size: 35px;
        padding-bottom: 4%;
    }
    .Hire__Side{
        height: 450px;
    }
    .Hire__Side p{
        width: 85%;
        margin: auto;
        font-size: 14px;
    }
}

