.Services__head{
    background: linear-gradient(to left, #000000e3, #04dccade) , url('../../public/office.png');
    width: 100%;
    height: 550px;
    background-position: bottom;
}

.Services__head h1{
    width: 68%;
    margin: auto;
    padding-top: 13%;
    color: #fff;
    line-height: 1;
    font-weight: bolder;
}

.Services__head span{
    color: #d99a07;
}

.Services__head p{
    color: #fff;
    font-size: 18px;
    margin-top: 1%;
}

.Services__head button{
    background-color: #d99a07;
    padding: 5px 25px;
    border: none;
    margin-top: 4%;
}

.ux, .web__app, .Mobile, .graphic{
    padding: 5%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    height: 350px;
}

.ux{
    background: linear-gradient(to left, #04dccaec, #000000f3), url('../../public/ux.jpg');
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
}

.hire{
    background: linear-gradient(to left, #04dccaec, #000000f3), url('../../public/hire.jpg');
    background-attachment: fixed;
    background-size: cover;
}

.web__app{
    background:linear-gradient(to right, #07d97adf, #000000f3), url('../../public/developer.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}

.graphic{
    background:linear-gradient(to right, #07d97adf, #000000f3), url('../../public/game.png');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}

.ux h1{
    color: #fff;
    font-weight: bold;
    font-size: 60px;
    width: 30%;
    margin: auto;
    line-height: 1;
}

.ux p {
    color: #fff;
    transform: rotate(358deg);
}

.web__app h1{
    color: #fff;
    text-align: left;
}

.web__app p, .ux p, .Mobile p, .graphic p{
    padding: 5%;
    border-radius: 20px;
    box-shadow: 3px 5px 10px #000;
    width: 80%;
    line-height: 1.3;
    font-size: 15px;
}

.web__app p {
    color: #fff;
    transform: rotate(2deg);
}

.Mobile p{
    color: #fff;
    transform: rotate(358deg);
}

.graphic p{
    color: #fff;
    transform: rotate(2deg);
}

.graphic h1{
    color: #fff;
}

.Mobile{
    background: linear-gradient(to left, #04dccaec, #000000f3), url('../../public//frontend.jpg');
    background-position: center;
    background-attachment: fixed;
    /* background-color: #07d979; */
}
.Mobile h1{
    color: #fff;
}

.Mobile h1, .web__app h1, .graphic h1{
    line-height: 1;
    font-weight: bold;
    font-size: 60px;
    width: 30%;
    margin: auto;
}

.hire h1{
    width: 70%;
}

.start__project{
    width:70%;
    margin: auto;
    padding: 5%;
}

.start__project h1{
    font-weight: bold;
    font-size: 60px;
    color: #04dcca;
    text-shadow: 6px 7px 10px #000;
}

.start__project p{
    color: #797979;
    width: 70%;
    margin: auto;
    line-height: 1.3;
}

.start__project button{
    background-color: #04dcca;
    color: #fff;
    border: none;
    margin-top: 5%;
    padding: 6px 22px;
    border-radius: 10px;
}

.start__project span{
    font-family: 'Times New Roman', Times, serif;
}

.Services hr{
    border: 0px solid #fff;
}

.arrow{
    animation: arrows 1s infinite;
    transform: translateX(-75%);
}



@keyframes arrows{
    0%{
        transform: translateX(-75%);
        opacity: 0;
    }
    50%{
        transform: translateX(-60%);
        opacity: .5;
    }
    100%{
        transform: translateX(0%);
        opacity: 1;
    }
}

.Services__head i{
    transform: translateX(0%);
    animation: arrow 1s ease-in-out .4s infinite;
}

@keyframes arrow{
    0%{
        opacity: 0;
        transform: translateX(-70%);
    }
    100%{
        opacity: 1;
        transform: translateX(0%);
    }
}

/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .Services__head{
        text-align: left;
    }
    .Services__head h1{
        font-size: 27px;
        width: 90%;
        margin: auto;
        padding-top: 24%;
    }
    .Services__head span{
        color: #d99a07;
    }

    .Services__head p{
        font-size: 18px;
        width: 70%;
        margin: auto;
        /* padding-top: -41%; */
    }

    .ux, .web__app, .Mobile, .graphic{
        padding: 5%;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        height: 530px;
    }
    .Mobile h1, .web__app h1, .graphic h1, .hire h1, .ux h1{
        font-size: 45px;
        text-align: center;
        width: 90%;
        padding-bottom: 20%;
    }

    .Mobile h1, .hire h1, .ux h1{
        padding-bottom: 10%;
    }

    .web__app h1, .graphic h1{
        padding-top: 10%;
    }

    .graphic p{
        margin-bottom: -10%;
    }

    .web__app p, .ux p, .Mobile p, .graphic p{
        width: 100%;
        margin: auto;
        line-height: 1.3;
        font-size: 14px;
        text-align: left;
    }
    .web__app p, .graphic p{
        margin-top: 10%;
    }

    .start__project h1{
        font-size: 30px;
        line-height: 1;
        width:100%;
        text-shadow: 5px 2px 5px #000;
    }
    .start__project{
        width:80%;
    }
    .start__project p{
        width: 90%;
        margin: auto;
        line-height: 1.2;
        font-size: 14px;
        color: #797979;
    }

    .start__project button{
        margin-top: 5%;
        padding: 6px 22px;
        font-size: 14px;
    }

    .start__project a{
        font-size: 14px;
    }

    .arrow{
        display: none;
    }
}


/* Devices under 768px (md) */

@media(max-width: 767px){
    .Services__head{
        text-align: left;
    }
    .Services__head h1{
        font-size: 22px;
        width: 90%;
        margin: auto;
        padding-top: 34%;
        font-weight: bolder;
        color: #fff;
    }
    .Services__head span{
        color: #d99a07;
    }

    .Services__head p{
        font-size: 16px;
        width: 90%;
        /* padding-top: 100%; */
        margin: 5%;
        line-height: 1;
        color: #fff;
        font-weight: bold;
        
    }

    .start__project{
        width:100%;
    }
    .start__project p{
        width: 90%;
        margin: auto;
        line-height: 1;
    }

    .start__project button{
        margin-top: 10%;
        padding: 4px 16px;
        font-size: 14px;
    }
    .start__project a{
        font-size: 14px;
    }
    .Services__head button{
        border-radius: 5px;
        margin-left: 5%;
    }
}

/* Devices under 576px (sm) */

@media(max-width: 575px){
    .Services__head h1{
        padding-top: 49%;
    }
    .start__project a{
        font-size: 14px;
    }
}