.Contact {
    padding-bottom: 5%;
}
.Contact > h1{
    color: #6e6262;
    margin-top: 2%;
    text-transform: uppercase;
    font-weight: bolder;
}

.Contact > p{
    font-style: italic;
    width: 80%;
    margin: auto;
    line-height: 1.4;
    color: #6e6262;
    padding-top: 5%;
    padding-bottom: 5%;
}

.Hire__Team{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 90%;
    margin: auto;
    margin-top: 5%;
}

.Hire__Single{
    background-color: #ececec;
    color: #6e6262;
    width: 85%;
    margin: auto;
    margin-left: 3%;
    padding: 2% 8%;
    margin-bottom: 4%;
    border-radius: 5px;
}

.Hire__Single a{
    color: #04dcca;
}

.Hire__Single a:hover{
    color: #04dcca;
    text-decoration: none;
}

.Hire__Single p{
    line-height: 1.2;
}

/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .Contact > h1{
        font-size: 30px;
        width: 95%;
        margin: auto;
        margin-top: 3%;
    }

    .Hire__Team{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        width: 100%;
        margin-top: 5%;
    }

    .Hire__Single h2{
        font-size: 24px;
    }
    .Hire__Single p{
        font-size: 16px;
    }

    .Contact > p{
        font-size: 14px;
        width: 95%;
        margin: auto;
    }
    .Hire__Single{
        width: 100%;
        margin-left: 0%;
        background-color: #f0f0f0;
        box-shadow: 9px 8px 10px #0000002c;
    }
}