.Footer{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    /* margin-top: 5%; */
    padding-top: 5%;
    color: #494949;
    text-align: left;
    
}

.Mainfooter{
    background: linear-gradient(to right, #04dccae8, #07d97ae7), url('../../public/footer.svg');
    background-position: top;
    background-repeat: no-repeat;
    background-attachment: fixed;
    width: 100%;
    height: 600px;
}

.Footer h1{
    font-size: 20px;
    padding-left: 5%;
    font-weight: bolder;
}

.Footer li{
    list-style: none;
    font-size: 15px;
    line-height: 2;
}

.Footer li a{
    font-weight: bold;
    color: #494949;
    text-decoration: none;
}

.Footer li a:hover{
    color: #494949;
}

.Footer hr{
    width: 50%;
    margin-left: 5%;
    border-bottom: 1px solid #494949;
}

.Footerline{
    background-color: #494949;
    width: 90%!important;
}

.Footerlower a{
    color: #494949;
    font-weight: bold;
}

.Footerlower a:hover{
    color: #494949;
    text-decoration: none;
}

.Footerlower li{
    list-style: none;
}

.Footerlower ul{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    width: 70%;
    margin: auto;
}

.Copyright{
    padding-top: -5%;
    color:#494949;
}

.Copyright img{
    width: 160px;
}

.Copyright p{
    color:#494949;
    font-weight: bold;
    font-size: 14px;
    padding-top: -19%;
}

/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .Footer{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    .Footer h1{
        font-size: 16px;
        font-weight: bold;
    }

    .Footer li{
        font-size: 14px;
        line-height: 1.3;
    }

    .Footer hr{
        width: 50%;
        margin-left: 5%;
        border-bottom: .5px solid #494949;
    }

    .Footerline{
        width: 100%!important;
    }
    
    .Footerlower ul{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        width: 100%;
    }
    .Footerlower a{
        font-size: 14px;
    }
    .Mainfooter{
        height: 500px;
    }

    .Copyright img{
        width: 100px;
    }

    .Copyright p{
        color: #494949;
        font-weight: bold;
        font-size: 10px;
    }
}

/* Devices under 768px (md) */

@media(max-width: 767px){
    .Footer{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
    .Footerlower ul{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        width: 100%;
    }
    .Mainfooter{
        height: 730px;
    }
}

/* Devices under 576px (sm) */

@media(max-width: 575px){
    .Footer img{
        width: 90%;
    }
    .Footer h1{
        font-size: 14px;
    }
    .Footer li{
        font-size: 12px;
    }
    .Footer hr{
        border-bottom: .5px solid #494949;
    }

    .Footerlower a{
        font-size: 12px;
    }

    .Copyright{
        padding-top: 15%;
    }
    .Mainfooter{
        height: 680px;
    }
}