
.Mission{
    background: linear-gradient(to right, #04dccadf, #000000a6), url('../../public/mission.svg');
    background-position: center;
    background-color: #04dcca;
    margin-top: 4%;
    margin-bottom: 5%;
    padding: 5%;
}

.Mission_Goal h1{
    width: 60%;
    text-align: left;
    color: #fff;
    padding-bottom: 3%;
    padding-left: 5%;
    font-size: 50px;
}

.Mission_Goal hr{
    margin-top: -3%;
    border: 2px solid #fff;
    width: 5%;
    margin-left: 5%;
}

.Mission_Goal p{
    color: #04dcca;
    padding: 5%;
    font-style: italic;
    
}

.Mission_p{
    margin-top: 5%;
    margin-left: 10%;
    background-color: #fff;
    width: 70%;
    box-shadow: 5px 5px 10px #000;
    border-radius: 10px;
}

/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .Mission_Goal h1{
        width: 80%;
        font-size: 30px;
        font-weight: bold;
    }

    .Mission_Goal hr{
        width: 15%
    }
    .Mission_p{
        width: 80%;
        margin: auto;
    }
}

/* Devices under 768px (md) */

@media(max-width: 767px){
    .Mission_Goal hr{
        width: 25%
    }

    .Mission_p{
        width: 90%;
        font-size: 13px;
    }
}

/* Devices under 576px (sm) */

@media(max-width: 575px){
    .Mission_Goal h1{
        width: 80%;
        font-size: 20px;
    }

    .Mission_Goal hr{
        width: 20%;
        border: 1px solid #fff;
    }

    .Mission_p{
        width: 90%;
        font-size: 12px;
    }
}