.NotFound h1{
    font-size: 50px;
    width: 80%;
    margin: auto;
    padding-bottom: 2%;
    padding-top: 2%;
    font-weight: bolder;
    color: #04dcca;
    line-height: 1;
}

.NotFound p{
    font-size: 20px;
    color: #585858;
    width: 75%;
    margin: auto;
    padding-bottom: 5%;
}

.NotFound button{
    margin-left: 2%;
    background-color: #04dcca;
    padding: 6px 16px;
    border: none;
    border-radius: 5px;
}

/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .NotFound h1{
        font-size: 25px;
        width: 80%;
        text-align: left;
        padding-top: 5%;
    }

    .NotFound p{
        font-size: 14px;
        width: 80%;
        text-align: left;
        padding-bottom: 10%;
    }

    .NotFound button{
        margin-left: 2%;
        background-color: #04dcca;
        padding: 4px 13px;
        border: none;
        border-radius: 5px;
        font-size: 12px;
    }
}