.Navigation ul{
    grid-template-columns: repeat(4, 1fr);
}

.nav-link{
    font-weight: bolder;
}

.navbar-dark .navbar-nav .nav-link {
    color: #ffff!important;
}

.Navigation{
    border-bottom: 1px solid #d8d8d8a4;
    background: linear-gradient(to right, #04dccaa1, #07d97a96);
}

/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .nav-link {
        font-size: 16px;
    }
}

