.Experience{
    background-color: #f5f5f5;
}

.Experience__Topic{
    width: 60%;
    margin: auto;
}

.Experience__Topic h1{
    padding-top: 5%;
    font-weight: bold;
    text-transform: uppercase;
    color: #6e6262;
}

.Experience__Topic h3{
    font-weight: bold;
    color: #6e6262;
    font-size: 22px;
    padding-bottom: 5%;
}

.Experience__Form input, .Experience__Form label, .Experience__Form select{
    width: 75%;
    text-align: left;
    border-radius: 5px;
    background-color: transparent;
}

.Experience__Form input, .Experience__Form select{
    outline: none;
    border: 1px solid #04dcca;
    margin-bottom: 2%;
}

.Experience__Form label{
    margin-bottom: -2%;
}

.Experience__btn{
    background: #04dcca;
    color: #fff;
    padding: 4px 180px;
    border: none;
    border-radius: 5px;
    outline: none;
    font-weight: bold;
    margin-top: -4%;
    clear: both;
}

.Experience__Lower{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.Experience__Img{
    width: 60%;
    margin-left: 0%;
    border-radius: 20px 0px 20px 0px;
}

.Experience__Img__Two{
    margin-top: -10%;
    width: 60%;
    margin-left: 30%;
    border-radius: 0px 20px 0px 20px;
}

.Cred{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    width: 80%;
    margin: auto;
    margin-top: 3%;
}

.Cred__Single{
    background-color: #fff;
    width: 95%;
    margin: auto;
    height: 250px;
    border-radius: 10px 10px 0px 0px;
    box-shadow: 6px 7px 10px rgba(0, 0, 0, 0.219);
}

.Cred__Single h1{
    background-color: #04dcca;
    border-radius: 10px 10px 0px 0px;
    color: #fff;
    padding: 5% 2%;
    font-size: 30px;
    font-weight: bolder;
}

.Cred__Single:hover{
    transform: scale(1.1);
    transition-timing-function: ease-in-out;
    transition-duration: .3s;
    box-shadow: 6px 7px 10px rgba(0, 0, 0, 0.561);
    transform-origin: center;
}

.Cred__Single p{
    padding: 5% 4%;
}

.Cred__New{
    padding-bottom: 10%;
}



/*============ MEDIA QUERIES ============*/

/* Devices under 1024px (Xlg) */

@media(max-width: 1024px){
    .Experience__Topic{
        width: 97%;
    }

    .Experience__btn{
        padding: 4px 170px;
        margin-top: 4%;
        margin-bottom: 9%;
        font-size: 16px;
    }
    .Cred__Single h1{
        padding: 5% 2%;
        font-size: 20px;
    }

    .Cred__Single p{
        font-size: 16px;
    }
}

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .Experience__Topic{
        width: 97%;
    }

    .Experience__btn{
        padding: 4px 120px;
        margin-top: 4%;
        margin-bottom: 9%;
        font-size: 16px;
    }

    .Experience__Img{
        width: 70%;
        margin-left: 0%;
        border-radius: 20px 0px 20px 0px;
    }
    
    .Experience__Img__Two{
        margin-top: -10%;
        width: 70%;
        margin-left: 20%;
        border-radius: 0px 20px 0px 20px;
    }

    .Experience__Topic h3{
        font-size: 20px;
        width: 80%;
        margin: auto;
        padding-bottom: 5%;
        text-align: center;
    }

    .Cred__Single h1{
        padding: 5% 2%;
        font-size: 20px;
    }

    .Cred__Single p{
        font-size: 14px;
    }
}


/* Devices under 768px (md) */

@media(max-width: 767px){
    .Experience__Topic h1{
        padding-top: 5%;
        font-size: 20px;
        text-align: left;
    }

    .Experience__Topic{
        width: 97%;
        padding-left: 5%;
    }

    .Experience__Topic h3{
        font-size: 14px;
        width: 80%;
        padding-bottom: 5%;
        text-align: left;
    }

    .Experience__Form input, .Experience__Form label, .Experience__Form select{
        font-size: 14px;
        width: 90%;
    }
    .Experience__btn{
        padding: 4px 130px;
        margin-top: 4%;
        margin-bottom: 9%;
        font-size: 16px;
    }

    .Experience__Lower{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }

    .Cred{
        display: grid;
        grid-template-columns: repeat(1,1fr);
        width: 80%;
        margin: auto;
        margin-top: 10%;
    }

    .Cred__Single h1{
        padding: 5% 2%;
        font-size: 20px;
    }

    .Cred__Single{
        margin-bottom: 12%;
    }
}

/* Devices under 576px (sm) */

@media(max-width: 575px){
    .Cred__Single p{
        font-size: 16px;
    }
}