.CP{
    /* height: 400px; */
}

.CP p{
    background-color: #00000036;
    color: #000;
    width: 90%;
    margin: auto;
    border-radius: 10px;
    padding: 5%;
    text-align: justify;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.CP h1{
    padding-top: 5%;
    text-align: left;
    padding-left: 5%;
}

.CP h6{
    text-align: right;
    padding-top: 5%;
    padding-right: 5%;
}

.CP__Detail{
    display: grid;
    grid-template-columns: repeat(2,1fr);
}

/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .CP p{
        background-color: #00000036;
        color: #000;
        width: 95%;
    }
}

/* Devices under 768px (md) */

@media(max-width: 767px){
    .CP__Detail{
        padding-top: 5%;
    }
    .CP p{
        background-color: #00000036;
        color: #000;
        width: 95%;
        font-size: 14px;
        text-align: left;
    }
    .CP__Detail h1{
        font-size: 20px;
        width: 100%;
   }
   
   .CP__Detail h6{
      font-size: 10px;
      padding-top: 10%;
   }
}

/* Devices under 576px (sm) */

@media(max-width: 575px){
    .CP__Detail h1{
        font-size: 14px;
        width: 100%;
   }
   .CP__Detail h6{
    font-size: 10px;
    padding-top: 6%;
 }
}