.Partners{
    padding-top: 5%;
    margin-left: 1px;
    text-align: center;
    margin-bottom: 5%;
}

.Partners__Logo{
    width: 80%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(5,1fr);
}

.Partners img{
    /* padding-left: 15%; */
    box-shadow: 9px 8px 10px rgba(0, 0, 0, 0.048);
    border-radius: 15%;
}

.Partners h1{
    color: #6e6262;
    text-align: left;
    padding-left: 5%;
}

.Partners hr{
    font-size: 50px;
    border: 2px solid  #6e6262;
    width: 10%;
    margin-left: 5%;
}

/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .Partners{
        margin-bottom: 13%;
    }
    .Partners h1{
        font-size: 24px;
    }

    .Partners hr{
        border: 1px solid #6e6262;
        width: 20%;
        margin-bottom: 5%;
    }
    .Partners img{
        padding-left: 5%;
        width: 120px;
    }
}

/* Devices under 768px (md) */

@media(max-width: 767px){
    .Partners{
        margin-top: 0px;
        margin-left: 5%;
        text-align: left;
        margin-bottom: 0%;
    }
    .Partners h1{
        font-size: 18px;
        padding-bottom: -26%;
    }

    .Partners img{
        padding: 5% 10%;
        width: 95%;
    }
    .Partners hr{
        border: 1px solid #6e6262;
        margin-top: 0%;
        width: 20%;
        margin-left: 5%;
    }

    .Partners__Logo{
        margin-top: 0%;
        padding-bottom: 12%;
        width: 80%;
        display: grid;
        grid-template-columns: repeat(5,1fr);
    }
}

/* Devices under 576px (sm) */

@media(max-width: 575px){
    .Partners{
        /* margin-top: 17%; */
    }
}