.About__Main{
    /* padding: 5%; */
}

.About{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
     padding: 5%;
}

.About img{
    width: 100%;
}

.About__top{
    width: 90%;
}

.About__top span{
    font-size: 170px;
    color: #d99a07;
}

.About__top h1{
    color: #fff;
    font-weight: bold;
    font-size: 80px;
    text-align: left;
    line-height: .8;
    text-shadow: 2px 5px 10px #000;
}

.About p{
    text-align: left;
    font-size: 16px;
   color:  #000;
   margin-top: 4%;
   box-shadow: 8px 9px 10px #000;
   padding: 5%;
   border-radius: 15px;
   font-style: oblique;
   font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   line-height: 1.4;
}

.Join{
    background: linear-gradient(to left, #d99a07e7, #000000dc), url('../../public/office.png');
    background-attachment: fixed;
    background-position: bottom;
     padding: 2%;
}

.Join h1{
    text-align: center;
    font-size: 70px;
    font-weight: bold;
    margin-top: 5%;
    line-height: 1;
    color: #d99a07;
    text-shadow: 7px 5px 10px #000;
}

.Join p{
    width: 70%;
    margin: auto;
    padding-top: 1%;
    color: #fff;
    line-height: 1.3;
}

.Join a, .Join button{
    color: #d99a07;
    background-color: #fff;
    padding: 6px 18px;
    margin-top: 3%;
    border: none;
    margin-bottom: 3%;
}

.Join a:hover{
    color: #d99a07;
}

.Engineer{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
    padding: 5%;
}

.Engineer h1{
    font-size: 90px;
    font-weight: bold;
    text-transform: uppercase;
    width: 100%;
    line-height: .9;
    text-shadow: 3px 5px 10px #000;
}

.Engineer img{
    width: 100%;
}

.Engineer span{
    color: #04dcca;
}

.Engineer p{
    color: #585858;
    padding-top: 3%;
}

.Engineer button{
    margin-top: 10%;
    padding: 7px 18px;
    background-color: #04dcca;
    border: none;
}

.Engineer a{
    color: #fff;
}

/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .About{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        width: 100%;
    }
    .About__top h1{
        font-size: 30px;
        padding-top: 8%;
        width: 100%;
    }
    .About__top span{
        font-size: 80px;
    }
    .About p{
        font-size: 14px;
       margin-top: 15%;
       text-align: justify;
       box-shadow: none;
       width: 100%;
    }
    .About img{
        width: 100%;
        margin-top: 35%;
    }
    .Join{
        /* margin-top: 100%; */
    }

    .Join h1{
        font-size: 60px;
    }
    .Join p{
        font-size: 14px;
    }
    .Join a, .Join button{
        padding: 3px 7px;
        font-size: 14px;
        margin-top: 3%;
    }
    .Engineer{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    .Engineer h1{
        font-size: 40px;
        line-height: 1;
    }
    .Engineer p{
        font-size: 14px;
    }
    .Engineer button{
        margin-top: 4%;
    }
}

/* Devices under 768px (md) */

@media(max-width: 767px){
    .About{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
    .About__top h1{
        font-size: 30px;
        padding-top: 8%;
        width: 100%;
    }
    .About__top span{
        font-size: 80px;
    }
    .About p{
        font-size: 14px;
       margin-top: 100%;
       text-align: justify;
       box-shadow: none;
       width: 100%;
    }
    .About img{
        width: 100%;
        margin-top: -300%;
    }
    .Join{
        /* margin-top: 100%; */
    }

    .Join h1{
        font-size: 30px;
    }
    .Join p{
        width: 90%;
        font-size: 12px;
    }
    .Join a, .Join button{
        padding: 3px 7px;
        font-size: 14px;
        margin-top: 8%;
    }
    .Engineer{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }

    .Engineer h1{
        font-size: 40px;
        line-height: 1;
    }
    .Engineer p{
        font-size: 14px;
    }
}

/* Devices under 576px (sm) */

@media(max-width: 575px){
    .About img{
        width: 100%;
        margin-top: -250%;
    }
}