.Teamwork{
    padding-top: 5%;
    padding-left: 5%;
    /* color: #d99a07; */
}

.Teamwork_msg{
    color: #07d979;
}

.Teamwork h1{
    font-size: 80px;
    font-weight: bold;
    width: 30%;
    text-align: left;
    line-height: 1;
    text-shadow: 5px 5px 10px #000;
}

.Teamwork_Main{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
}

.Teamwork_Main img{
    width: 80%;
}

/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .Teamwork h1{
        font-size: 49px;
        text-shadow: 2px 2px 5px #000000;
    }
    .Teamwork_Main{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    .Teamwork_Main img{
        width: 80%;
    }

    .Teamwork{
        height: 260px;
    }
}

/* Devices under 768px (md) */

@media(max-width: 767px){
    .Teamwork h1{
        font-size: 23px;
        text-shadow: 2px 2px 5px #000000;
    }

    .Teamwork_Main img{
        width: 90%;
        position: absolute;
    }

    .Teamwork_Main{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
}

/* Devices under 576px (sm) */

@media(max-width: 575px){
    .Teamwork h1{
        font-size: 22px;
        text-shadow: 2px 2px 5px #000000;
    }
}
