.Developers{
    background-color: #07d979;
    padding: 5%;
    margin-top: 5%;
}

.Developers h1{
    color: #fff;
    font-size: 60px;
    text-align: left;
    width: 40%;
    font-weight: bolder;
    line-height: 1;
    text-transform: uppercase;
}

.Developers h1 span{
    color: #000;
}

.Developers hr{
    width: 15%;
    border: 2px solid #fff;
    margin-left: .5%;
}


.Developers_info{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-top: 6%;
}

.Developers_info img{
    width: 100%;
}

.Developers_desc img{
    width: 40%!important;
}

.Developers_desc{
    color: #fff;
    font-weight: bold;
    text-align: right;
}

table{
    width:100%;
    border-collapse: collapse;
    text-align: center;
    margin-top: 9%;
}

td{
    padding-bottom: 5%;
}



/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .Developers{
        margin-top: 13%;
    }
    .Developers h1{
        font-size: 40px;
        line-height: 1;
    }
    .Developers_desc{
        text-align: center;
        font-size: 12px;
        margin-top: 16%;
    }
    .Developers_desc img{
        width: 50%!important;
    }
}

/* Devices under 768px (md) */

@media(max-width: 767px){
    .Developers{
        margin-top: 5%;
    }
    .Developers_desc{
        font-size: 10px;
        padding-top: 10%;
    }
}

/* Devices under 576px (sm) */

@media(max-width: 575px){
    .Developers{
        padding-top: 13%;
    }
    .Developers h1{
        font-size: 30px;
    }
    .Developers_info{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        padding-top: 16%;
    }
    .Developers_desc{
        text-align: center;
        font-size: 14px;
        padding-top: 10%;
    }
    .Developers_desc img{
        width: 50%!important;
    }
}