.Clientproject{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
}

.ClientText{
    width: 80%;
    text-align: left;
    margin-left: 5%;
}

.ClientText h1{
    font-size: 50px;
    font-weight: bolder;
    text-transform: uppercase;
}

.ClientText span{
    color: #04dcca;
}

.Clientproject img{
    width: 100%;
}

.Clientproject button{
    background-color: #04dcca;
    color: #fff;
    padding: 10px 25px;
    border: none;
    outline: none;
    border-radius: 15px;
    margin-top: 7%;
}

/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .Clientproject{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        width: 100%;
    }

    .Clientproject img{
        width: 100%;
    }
    .ClientText{
        width: 90%;
        text-align: center;
    }

    .ClientText h1{
        font-size: 30px;
    }

    .ClientText p{
        font-size: 12px;
    }
    .Clientproject button{
        padding: 4px 16px;
        font-size: 14px;
        font-weight: bold;
        margin-top: 1%;
    }
}

/* Devices under 768px (md) */

@media(max-width: 767px){
    .Clientproject{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        width: 100%;
    }
    .ClientText{
        width: 90%;
        text-align: center;
        position: absolute;
        padding-top: 60%;
    }
    .Clientproject img{
        width: 95%;
        padding-bottom: 80%;
    }
}

