.Career{
    padding-bottom: 8%;
}
.Career__Team{
    background: linear-gradient(to left, #04dccadc, #000000d2 ), url('../../public/developer.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    height: 300px;
}

.Career__Team h1{
    color: #fff;
    font-size: 70px;
    padding-top: 7%;
    font-weight: bold;
}

.Career__Team p{
    color: #fff;
    font-size: 18px;
}

.Career__Enigineer h1{
    font-size: 30px;
    padding-top: 5%;
}

.Career__Enigineer{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 5%;
}

.Engineers{
    width: 90%;
    box-shadow: 4px 6px 10px rgba(0, 0, 0, 0.507);
    padding: 2%;
    border-radius: 10px;
}

.Engineers button{
    padding: 3px 26px;
    background-color: #04dcca;
    border: none;
    margin-bottom: 5%;
}

.recruit{
    font-style: italic;
}
/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .Career__Team h1{
        font-size: 50px;
        padding-top: 10%;
    }
    .Career__Team p{
        font-size: 20px;
    }
    .Career__Enigineer{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        width: 100%;
        text-align: center;
    }
    .Career__Enigineer h1{
        font-size: 20px;
       color: #04dcca;
    }
    .Engineers{
        width: 100%;
        box-shadow: 4px 6px 10px #0000003b;
    }
    .Engineers p{
        font-size: 14px;
    }
    .recruit{
         width: 90%;
         margin: auto;
         font-size: 14px;
    }
}

/* Devices under 768px (md) */

@media(max-width: 767px){
    .Career__Team h1{
        font-size: 30px;
        padding-top: 24%;
        text-align: left;
        padding-left: 5%;
    }
    .Career__Team p{
        font-size: 16px;
        text-align: left;
        padding-left: 5%;
        width: 90%;
        line-height: 1.1;
    }
    .Career__Enigineer{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        width: 100%;
        text-align: left;
        /* padding: 5%; */
    }
    .Career__Enigineer h1{
        font-size: 20px;
       color: #04dcca;
    }
    .Engineers{
        width: 100%;
        box-shadow: 4px 6px 10px #0000003b;
    }
    .Engineers p{
        font-size: 14px;
    }
    .recruit{
         width: 90%;
         margin: auto;
         font-size: 14px;
    }
}

/* Devices under 576px (sm) */

@media(max-width: 575px){
    .Career__Team p{
        font-size: 14px;
    }
    .Career__Enigineer p{
        font-size: 12px;
    }
    .Engineers button{
        padding: 2px 16px;
        font-size: 16px;
    }
    .Engineers a{
        font-size: 14px;
    }
}