.Header{
    background: linear-gradient(to right, #04dccae8, #07d97ae7), url('../../../public/top.svg');
    width: 100%;
    height: 600px;
    background-size: cover;
    background-position: center;
}

.Headline{
    color: #fff;
    width: 60%;
    margin: auto;
    padding-top: 7%;
    /* margin-top: 4%; */
}

.Headline h1{
    font-weight: bolder;
}

.Headline h3{
    width: 70%;
    margin: auto;
    font-size: 20px;
}

.Talent{
    color: #d99a07;
}

.Header button{
    background-color: #d99a07;
    padding: 8px 25px;
    border: none;
    outline: none;
    margin-top: 3%;
}

button > a{
    color: #fff;
    font-weight: bolder;
}

button > a:hover{
    color: #fff;
    text-decoration: none;
}

.Lower_header{
    margin: auto;
    font-weight: bold;
    margin-top: 8%;
    color: #ffffff;
    width: 60%;
}

.fluid-container i{
    transform: translateX(0%);
    animation: arrow 1s ease-in-out .4s infinite;
}

@keyframes arrow{
    0%{
        opacity: 0;
        transform: translateX(-70%);
    }
    100%{
        opacity: 1;
        transform: translateX(0%);
    }
}

/*============ MEDIA QUERIES ============*/

/* Devices under 992px (lg) */

@media(max-width: 991px){
    .Header{
        height: 500px;
        color: #fff;
        text-align: left;
    }
    .Headline{
        width: 90%;
        padding-top: 10%;
        text-align: left;
    }

    .Headline h1{
        font-size: 44px;
    }

    .Headline h3{
        width: 90%;
        font-size: 18px;
        margin-left: 0px;
    }

    .Header button{
        padding: 10px 24px;
        margin-top: 10%;
        font-size: 16px;
        margin-left: 5%;
    }
    .Lower_header{
        display: none;
    }
}

/* Devices under 768px (md) */

@media(max-width: 767px){
    .Headline{
        width: 90%;
        padding-top: 30%;
        text-align: left;
        color: #fff;
    }
    .Headline h1{
        font-size: 22px;
    }
    .Headline h3{
        width: 90%;
        font-size: 16px;
    }

    .Header button{
        padding: 4px 16px;
        margin-top: 10%;
        font-size: 16px;
        border-radius: 5px;
    }

    .Lower_header{
        margin: auto;
        font-weight: bold;
        margin-top: 8%;
        color: #ffffff;
        width: 90%;
    }
}

/* Devices under 576px (sm) */

@media(max-width: 575px){
    .Headline{
        padding-top: 30%;
        width: 90%;

    }
    .Headline h1{
        font-size: 27px;
    }
    .Headline h3{
        font-size: 14px;
    }
    .Header button{
        padding: 7px 16px;
        margin-top: 10%;
        font-size: 14px;
        border-radius: 5px;
        text-align: left;
    }

    .Header a{
        text-align: left;
    }
}